
/**
 *    em       
 * (  px) .
 * @param  {number} $px    , - 
 * @param  {string} $font-size  ,  .   
 * @return {string}   $px  em   $font-size
 */

$font-size:                   14px !default;

@function px-to-em($px, $font-size: $font-size) {
  @return (($px * 100 / ($font-size / 1px)) / 100) * 1em;
}
