//        - swipe,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.swipe {

  $block-name:                &;
  // #{$block-name}__element

  overflow: hidden;
  visibility: hidden;
  position: relative;
}

.swipe-wrap {
  overflow: hidden;
  position: relative;

  & > div {
    float: left;
    width: 100%;
    position: relative;
    overflow: hidden;
  }
}
